import styled from 'styled-components/macro';
import {colors} from '../config/styles';
import Highlight, {defaultProps} from 'prism-react-renderer';

console.log({defaultProps});

const Code = styled.code`
    ${colors.withVar('primary', 'color')};
`;

export default function CodeBlock({
    children,
    wide = false,
    className = 'language-plain'
}: {
    live?: boolean,
    wide?: boolean,
    children: string,
    className: string
}) {
    return (
        <Highlight
            {...defaultProps}
            code={children.trim()}
            language={className.split('-').pop()}
        >
            {({style, tokens, getLineProps, getTokenProps}) => (
                <pre style={{overflow: 'auto', ...style, width: wide && '100%'}}>
                    {tokens.map((line, lnum) => (
                        <Code
                            key={lnum}
                            {...{...getLineProps({line, key: lnum}), className: null}}
                        >
                            {line.map((token, lcol) => (
                                <span
                                    key={lcol}
                                    {...{
                                        ...getTokenProps({token, key: lcol}),
                                        className: null
                                    }}
                                />
                            ))}
                        </Code>
                    ))}
                </pre>
            )}
        </Highlight>
    );
}
